<template>
	<div id="main">
	  <div id="container">
		<div id="left">
			<div id="icon">
				<router-link to="/"><i class="fa fa-home" aria-hidden="true" /></router-link>
				<router-link to="/alarm"><i class="fa fa-warning" aria-hidden="true" /></router-link>
				<router-link to="/traffic" id="selected"><i class="fa fa-street-view" aria-hidden="true" /></router-link>
			</div>
		</div>
		<div id="right">
		  <div id="title">
			<div id="word">&ensp;<i class="fa fa-street-view" aria-hidden="true" />&ensp;人流量统计(分钟)</div>
		  </div>
		  <div id="time">
			<div id="input">
				<div style="flex:15;">
					<div class="input-group mb-3">
						<div class="input-group-prepend">
							<span class="input-group-text" id="basic-addon">请输入开始时间:</span>
						</div>
						<input type="text" class="form-control" v-model="start_y">
						<div class="input-group-append">
							<span class="input-group-text">年</span>
						</div>
						<input type="text" class="form-control" v-model="start_m">
						<div class="input-group-append">
							<span class="input-group-text">月</span>
						</div>
						<input type="text" class="form-control" v-model="start_d">
						<div class="input-group-append">
							<span class="input-group-text">日</span>
						</div>
						<input type="text" class="form-control" v-model="start_h">
						<div class="input-group-append">
							<span class="input-group-text">时</span>
						</div>
					</div>
					<div class="input-group mb-3">
						<div class="input-group-prepend">
							<span class="input-group-text" id="basic-addon">请输入结束时间:</span>
						</div>
						<input type="text" class="form-control" v-model="end_y">
						<div class="input-group-append">
							<span class="input-group-text">年</span>
						</div>
						<input type="text" class="form-control" v-model="end_m">
						<div class="input-group-append">
							<span class="input-group-text">月</span>
						</div>
						<input type="text" class="form-control" v-model="end_d">
						<div class="input-group-append">
							<span class="input-group-text">日</span>
						</div>
						<input type="text" class="form-control" v-model="end_h">
						<div class="input-group-append">
							<span class="input-group-text">时</span>
						</div>
					</div>
				</div>
				<div id="refresh">
					<button type="button" class="btn btn-success" @click="refresh(start_y,start_m,start_d,start_h,end_y,end_m,end_d,end_h)">查询</button>
				</div>
			</div>
		  </div>
		  <div id="list">
			<table class="table table-bordered table-striped table-hover">
			  <thead>
				<tr>
				  <th>拍摄时间</th>
				  <th>人流量增量</th>
				  <th>平均可视人流</th>
				</tr>
			  </thead>
			  <tbody>
				<tr v-for="i in content">
				  <td>{{ i.time_frame }}</td>
				  <td>{{ i.delta_count }}</td>
				  <td>{{ i.avg_vis }}</td>
				</tr>
			  </tbody>
			</table>
		  </div>
		  <div id="footer">
			<nav id="nav">
				<ul class="pagination">
					<li class="page-item">
					<a class="page-link">
						<span aria-hidden="true" @click="refresh_pre(start_y,start_m,start_d,start_h,end_y,end_m,end_d,end_h)">&laquo;上一页</span>
					</a>
					</li>
					<li class="page-item active"><a class="page-link" @click="refresh(start_y,start_m,start_d,start_h,end_y,end_m,end_d,end_h)">{{ page }}</a></li>
					<li class="page-item">
					<a class="page-link">
						<span aria-hidden="true" @click="refresh_next(start_y,start_m,start_d,start_h,end_y,end_m,end_d,end_h)">&raquo;下一页</span>
					</a>
					</li>
				</ul>
				<ul><div id="num">共{{ page_num }}页</div></ul>
			</nav>
		  </div>
		</div>
	  </div>
	</div>
</template>
  
<script>
	export default{
		data(){
			return{
				content:null,
				page:1,
				start_y:2023,
				start_m:1,
				start_d:1,
				start_h:0,
				end_y:2023,
				end_m:5,
				end_d:1,
				end_h:0,
				page_num:0,
			}
		},
		methods:{
			refresh(start_y,start_m,start_d,start_h,end_y,end_m,end_d,end_h){
				this.axios.get('http://localhost:8000/api/display/',{
					params:{
						page:this.page,
						start_y:start_y,
						start_m:start_m,
						start_d:start_d,
						start_h:start_h,
						end_y:end_y,
						end_m:end_m,
						end_d:end_d,
						end_h:end_h,
					}
				})
				.then(res=>{
					this.page_num=res.data.page_num
					this.content=res.data.result
				})
			},
			refresh_pre(start_y,start_m,start_d,start_h,end_y,end_m,end_d,end_h){
				if(this.page-1<1){
					return
				}
				this.page=this.page-1,
				this.axios.get('http://localhost:8000/api/display/',{
					params:{
						page:this.page,
						start_y:start_y,
						start_m:start_m,
						start_d:start_d,
						start_h:start_h,
						end_y:end_y,
						end_m:end_m,
						end_d:end_d,
						end_h:end_h,
					}
				})
				.then(res=>{
					this.page_num=res.data.page_num
					this.content=res.data.result
				})
			},
			refresh_next(start_y,start_m,start_d,start_h,end_y,end_m,end_d,end_h){
				if(this.page+1>this.page_num){
					return
				}
				this.page=this.page+1,
				this.axios.get('http://localhost:8000/api/display/',{
					params:{
						page:this.page,
						start_y:start_y,
						start_m:start_m,
						start_d:start_d,
						start_h:start_h,
						end_y:end_y,
						end_m:end_m,
						end_d:end_d,
						end_h:end_h,
					}
				})
				.then(res=>{
					this.page_num=res.data.page_num
					this.content=res.data.result
				})
			},
		}
	}
</script>
  
<style scoped>
#main{
	width: 100vw;
	height: 100vh;
	box-sizing: border-box;
}
#container{
	width: 100%;
	height: 90%;
	position: relative;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
	background-color: #fff;
	box-shadow: 0 0 3px rgb(57,167,176),
				0 0 6px rgb(56,183,145);
	display: flex;
}
#left{
	flex: 1;
	background-color: #eeeeee;
	box-shadow: inset -15px 0px 10px -10px #666666;
}
#left #icon{
	font-size: 50px;
	text-align: center;
	position: relative;
	left: 50%;
	transform: translate(-50%);
}
#right{
	flex: 20;
	display: flex;
	flex-direction: column;
}
#title{
	flex: 1;
	display: flex;
	flex-direction: column;
	justify-content: center;
	background-image: linear-gradient(135deg,rgb(57,167,176),rgb(56,183,145));
}
#word{
	font-size: 3em;
	font-weight: lighter;
	color: #fff;
}
#time{
	width: 100%;
	height: 15%;
	padding: 1%;
}
#input{
	display: flex;
}
#basic-addon{
	color: #fff;
	background-color: rgb(57,167,176);
}
#refresh{
	flex: 1;
	display: flex;
	align-items: center;
	justify-content: center;
}
#list{
	flex: 5;
	overflow: hidden;
	padding: 1%;
}
#footer{
	display: flex;
	flex: 0.5;
}
#nav{
	display: flex;
	position: relative;
	top: 65%;
	left: 50%;
	transform: translate(-50%,-50%);
}
#num{
	margin-top: .3em;
	font-size: 1.1em;
	font-weight: lighter;
}
a{
	color: #666666;
	transition: all 0.2s;
}
a:hover{
	color: #0088ff;
	transition: all 0.2s;
}
#selected{
	color: #0088ff;
}
</style>